// src/pages/Termine.js
import React, { useEffect, useMemo, useState } from "react";
import { Container, Box, Typography, Stack, Button, alpha, Chip } from "@mui/material";
import { motion } from "framer-motion";
import { CalendarMonth, ChevronLeft, ChevronRight } from "@mui/icons-material";

/* --------------------------- Kalender helpers -------------------------- */
const addDays = (d, n) => {
    const x = new Date(d);
    x.setDate(x.getDate() + n);
    return x;
};
const startOfDay = (d) => new Date(d.getFullYear(), d.getMonth(), d.getDate());
const pad2 = (n) => String(n).padStart(2, "0");
const dayKeyLocal = (d) => {
    const x = startOfDay(d);
    return `${x.getFullYear()}-${pad2(x.getMonth() + 1)}-${pad2(x.getDate())}`;
};
const parseISODateLocal = (iso) => {
    const [y, m, day] = String(iso).split("-").map(Number);
    return new Date(y, (m || 1) - 1, day || 1);
};
const norm = (s) => String(s || "").trim().toLowerCase();
const weekdayLabel = (d) => d.toLocaleDateString("de-DE", { weekday: "long" });
const dateLabel = (d) => d.toLocaleDateString("de-DE", { day: "2-digit", month: "2-digit" });

function startOfIsoWeek(d) {
    const x = startOfDay(d);
    const day = x.getDay();
    const diff = (day === 0 ? -6 : 1 - day);
    return addDays(x, diff);
}

const TypeChip = ({ kind }) => {
    return (
        <Chip
            size="small"
            label={kind}
            sx={{
                height: 22,
                fontSize: 12,
                fontWeight: 800,
                background: alpha("#27C2D3", 0.14),
                border: `1px solid ${alpha("#0B1B24", 0.10)}`,
            }}
        />
    );
};

const CancelChip = () => (
    <Chip
        size="small"
        label="Abgesagt"
        sx={{
            height: 22,
            fontSize: 12,
            fontWeight: 900,
            color: alpha("#0B1B24", 0.78),
            background: alpha("#0B1B24", 0.06),
            border: `1px dashed ${alpha("#0B1B24", 0.26)}`,
        }}
    />
);

function buildEventsInRange({ data, rangeStart, rangeDays }) {
    const start = startOfDay(rangeStart);
    const end = addDays(start, rangeDays);

    const wiederkehrend = Array.isArray(data?.wiederkehrend) ? data.wiederkehrend : [];
    const termine = Array.isArray(data?.termine) ? data.termine : [];

    const cancels = new Set(
        termine
            .filter((t) => t && t.type === "cancel" && t.date && t.title)
            .map((c) => `${String(c.date).trim()}|${norm(c.title)}`)
    );

    const events = [];

    for (let i = 0; i < rangeDays; i++) {
        const d = addDays(start, i);
        const weekday = d.getDay();
        wiederkehrend.forEach((r) => {
            if (weekday === r.weekday) {
                const title = r.title || "Training";
                const key = `${dayKeyLocal(d)}|${norm(title)}`;
                events.push({
                    kind: r.kind || "training",
                    title,
                    time: r.time || "",
                    location: r.location || "",
                    date: new Date(d),
                    cancelled: cancels.has(key),
                });
            }
        });
    }

    termine
        .filter((t) => t && t.type === "event" && t.date)
        .forEach((t) => {
            const d = parseISODateLocal(t.date);
            if (d >= start && d < end) {
                const title = t.title || "Termin";
                const key = `${dayKeyLocal(d)}|${norm(title)}`;
                events.push({
                    kind: t.kind || "event",
                    title,
                    time: t.time || "",
                    location: t.location || "",
                    date: d,
                    cancelled: cancels.has(key),
                });
            }
        });

    events.sort((a, b) => a.date - b.date);
    return events;
}

function groupEventsByDay({ events, rangeStart, rangeDays }) {
    const map = new Map();
    events.forEach((e) => {
        const k = dayKeyLocal(e.date);
        if (!map.has(k)) map.set(k, []);
        map.get(k).push(e);
    });

    const days = [];
    const start = startOfDay(rangeStart);
    for (let i = 0; i < rangeDays; i++) {
        const d = addDays(start, i);
        const k = dayKeyLocal(d);
        days.push({ date: d, key: k, events: map.get(k) || [] });
    }
    return days;
}

/* ---------------------------------------------------------------------- */

export default function Termine() {
    const [data, setData] = useState(null);
    const [page, setPage] = useState(0); // 0 = aktuelle 2 Wochen, +/- 1 = +/- 2 Wochen

    const todayKey = useMemo(() => dayKeyLocal(new Date()), []);

    useEffect(() => {
        fetch(process.env.PUBLIC_URL + "/content/termine.json")
            .then((r) => {
                if (!r.ok) throw new Error(`HTTP ${r.status} beim Laden von /content/termine.json`);
                return r.json();
            })
            .then(setData)
            .catch(() => setData({ wiederkehrend: [], termine: [] }));
    }, []);

    const rangeStart = useMemo(() => addDays(startOfIsoWeek(new Date()), page * 14), [page]);

    const days = useMemo(() => {
        if (!data) return [];
        const events = buildEventsInRange({ data, rangeStart, rangeDays: 14 });
        return groupEventsByDay({ events, rangeStart, rangeDays: 14 });
    }, [data, rangeStart]);

    const rangeLabel = useMemo(() => {
        const a = rangeStart;
        const b = addDays(rangeStart, 13);
        const aTxt = a.toLocaleDateString("de-DE", { day: "2-digit", month: "2-digit" });
        const bTxt = b.toLocaleDateString("de-DE", { day: "2-digit", month: "2-digit" });
        return `${aTxt} – ${bTxt}`;
    }, [rangeStart]);

    if (!data) return <div style={{ padding: 16 }}>Lade Termine…</div>;

    return (
        <Container maxWidth="lg" sx={{ mt: { xs: 3, md: 4 } }}>
            <Box
                component={motion.div}
                initial={{ opacity: 0, y: 10 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.35 }}
                sx={{
                    p: { xs: 2.2, md: 3.2 },
                    borderRadius: 4,
                    background: alpha("#FFFFFF", 0.88),
                    border: `1px solid ${alpha("#0B1B24", 0.10)}`,
                    boxShadow: "0 18px 55px rgba(11,27,36,0.10)",
                    backdropFilter: "blur(12px)",
                }}
            >
                <Stack
                    direction={{ xs: "column", sm: "row" }}
                    spacing={1.2}
                    alignItems={{ xs: "stretch", sm: "center" }}
                    justifyContent="space-between"
                    sx={{ mb: 1.4 }}
                >
                    <Stack direction="row" spacing={1} alignItems="center">
                        <Box
                            sx={{
                                width: 42,
                                height: 42,
                                borderRadius: 999,
                                display: "grid",
                                placeItems: "center",
                                background: alpha("#27C2D3", 0.12),
                                border: `1px solid ${alpha("#27C2D3", 0.22)}`,
                            }}
                        >
                            <CalendarMonth />
                        </Box>
                        <Box>
                            <Typography variant="h3" sx={{ lineHeight: 1.05 }}>
                                Termine
                            </Typography>
                            <Typography variant="body2" sx={{ color: "text.secondary" }}>
                                2 Wochen (Mo–So) • {rangeLabel}
                            </Typography>
                        </Box>
                    </Stack>

                    <Stack direction="row" spacing={1}>
                        <Button variant="outlined" startIcon={<ChevronLeft />} onClick={() => setPage((p) => p - 1)}>
                            Zurück
                        </Button>
                        <Button variant="outlined" onClick={() => setPage(0)}>
                            Heute
                        </Button>
                        <Button variant="outlined" endIcon={<ChevronRight />} onClick={() => setPage((p) => p + 1)}>
                            Weiter
                        </Button>
                    </Stack>
                </Stack>

                {/* 3 Spalten, aber vertikale Reihenfolge (CSS columns) */}
                <Box sx={{ columns: { xs: 1, sm: 2, md: 3 }, columnGap: "1.5rem" }}>
                    {days.map((day) => {
                        const isToday = day.key === todayKey;

                        return (
                            <Box
                                key={day.key}
                                sx={{
                                    breakInside: "avoid",
                                    mb: 1.5,
                                    p: 1.35, // etwas kleiner
                                    borderRadius: 2,
                                    border: `1px solid ${alpha("#0B1B24", isToday ? 0.16 : 0.08)}`,
                                    background: alpha("#fff", 0.70),
                                    position: "relative",
                                    overflow: "hidden",
                                    boxShadow: isToday ? "0 18px 55px rgba(39,194,211,0.22)" : "none",
                                    transform: isToday ? "translateY(-1px)" : "none",
                                }}
                            >
                                {isToday && (
                                    <>
                                        <Box
                                            sx={{
                                                position: "absolute",
                                                left: 0,
                                                top: 0,
                                                bottom: 0,
                                                width: 6,
                                                background: "#27C2D3",
                                                opacity: 0.95,
                                            }}
                                        />
                                        <Box
                                            sx={{
                                                position: "absolute",
                                                inset: 0,
                                                background:
                                                    "radial-gradient(420px 240px at 10% 15%, rgba(39,194,211,0.18), transparent 60%)",
                                                pointerEvents: "none",
                                            }}
                                        />
                                    </>
                                )}

                                <Stack spacing={0.8} sx={{ pl: isToday ? 1 : 0 }}>
                                    <Stack direction="row" spacing={1} alignItems="center" sx={{ flexWrap: "wrap" }}>
                                        <Typography sx={{ fontWeight: 950, textTransform: "capitalize", fontSize: 14 }}>
                                            {weekdayLabel(day.date)}
                                        </Typography>
                                        <Typography variant="body2" sx={{ color: "text.secondary" }}>
                                            {dateLabel(day.date)}
                                        </Typography>
                                        {isToday && (
                                            <Chip
                                                size="small"
                                                label="Heute"
                                                sx={{
                                                    height: 24,
                                                    fontSize: 12,
                                                    fontWeight: 900,
                                                    color: "#063A52",
                                                    background: alpha("#27C2D3", 0.20),
                                                    border: `1px solid ${alpha("#27C2D3", 0.42)}`,
                                                }}
                                            />
                                        )}
                                    </Stack>

                                    {day.events.length === 0 ? (
                                        <Typography variant="body2" sx={{ color: "text.secondary" }}>
                                            Keine Termine
                                        </Typography>
                                    ) : (
                                        <Stack spacing={0.9} sx={{ mt: 0.6 }}>
                                            {day.events.map((e, idx) => (
                                                <Box key={idx} sx={{ display: "flex", gap: 1, alignItems: "flex-start" }}>
                                                    <Stack direction="row" spacing={0.8} alignItems="center" sx={{ mt: "1px" }}>
                                                        <TypeChip kind={e.kind} />
                                                        {e.cancelled && <CancelChip />}
                                                    </Stack>

                                                    <Box sx={{ minWidth: 0 }}>
                                                        <Typography
                                                            sx={{
                                                                fontWeight: 850,
                                                                lineHeight: 1.2,
                                                                fontSize: 13.5,
                                                                textDecoration: e.cancelled ? "line-through" : "none",
                                                                opacity: e.cancelled ? 0.65 : 1,
                                                            }}
                                                        >
                                                            {e.title}
                                                        </Typography>
                                                        <Typography
                                                            variant="body2"
                                                            sx={{ color: "text.secondary", fontSize: 12.5, opacity: e.cancelled ? 0.65 : 1 }}
                                                        >
                                                            {(e.time || "").trim()}
                                                            {e.location ? ` • ${e.location}` : ""}
                                                        </Typography>
                                                    </Box>
                                                </Box>
                                            ))}
                                        </Stack>
                                    )}
                                </Stack>
                            </Box>
                        );
                    })}
                </Box>
            </Box>
        </Container>
    );
}
